.popup__input {
	border-top: 0;
	border-left: 0;
	border-right: 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
	width: 100%;
	height: 33px;
	padding: 0;
	margin: 0 auto;
	font-size: 14px;
	line-height: 17px;
}

.popup__input::focus {
	outline: none;
}

@media screen and (min-width:1280px) {
	.popup__input {
		width: 358px;
	}
}
